export const fontFamilies = fontFliesInfo.map(({ value }) => value);

export const getFontFamily = (t: (locale: string) => string) => {
  return fontFliesInfo.map((i) => ({ title: i.t ? t(i.title) : i.title, value: i.value, installable: i.installable }))
};
